.page-footer {
  margin-top: auto;
  color: #ffffff;
  background: #030a1a;
}

.page-footer__nav {
  display: flex;
  justify-content: space-between;
  width: $desktop-container-width - 40px;
  margin: 0 auto;
  padding: 42px 0 38px;
}

.page-footer__logo {
  margin: 2px 0 0;
}

.page-footer__logo a {
  display: block;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: bold;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #ffffff;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__menu {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 5px -60px 0 0;
  padding: 0;
}

.page-footer__menu li {
  margin: 0 40px 0 0;
}

.page-footer__menu a {
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-decoration: none;
  color: #ffffff;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__social {
  list-style: none;
  display: flex;
  margin: 0 -1px 0 0;
  padding: 0;
}

.page-footer__social-item {
  margin: 0 25px 0 0;

  &--vk {
    margin: 4px 25px 0 0;
  }
}

.page-footer__social-item:last-child {
  margin: 0;
}

.page-footer__social-item a {
  display: block;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__social-item svg {
  display: block;
}

@media (max-width: $desktop-container-width - 1) {
  .page-footer__nav {
    width: $tablet-container-width;
    padding: 45px 0 38px;
  }

  .page-footer__logo a {
    font-size: 24px;
    line-height: 24px;
  }

  .page-footer__menu {
    margin: 5px -49px 0 0;
  }

  .page-footer__menu li {
    margin: 0 30px 0 0;
  }

  .page-footer__social-item {
    margin: 0 20px 0 0;

    &--vk {
      margin: 4px 20px 0 0;
    }
  }
}

@media (max-width: $tablet-width - 1) {
  .page-footer__nav {
    position: relative;
    flex-direction: column;
    justify-content: flex-start;
    width: $mobile-container-width;
    padding: 48px 0 38px;
  }

  .page-footer__logo {
    position: absolute;
    left: 2px;
    top: 49px;
    margin: 0;
  }

  .page-footer__logo a {
    font-size: 20px;
    line-height: 20px;
  }

  .page-footer__menu {
    display: block;
    margin: 0 0 15px;
    padding: 0 0 0 2px;
    column-count: 2;
    column-gap: 29px;
  }

  .page-footer__menu li {
    display: inline-block;
    width: 100%;
    margin: 0 0 21px;
  }

  .page-footer__menu li:first-child {
    margin-top: 40px;
  }

  .page-footer__menu a {
    font-size: 16px;
    line-height: 19px;
  }

  .page-footer__social {
    padding: 0 0 0 2px;
  }

  .page-footer__social-item {
    margin: 0 35px 0 0;

    &--vk {
      padding: 5px 0 0;
    }

    &--vk svg {
      width: 32px;
      height: 17px;
    }

    &--instagram svg {
      width: 27px;
      height: 27px;
    }

    &--fb svg {
      width: 14px;
      height: 27px;
    }
  }
}
